import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Question } from 'src/app/models/initiative/question';

@Component({
  selector: 'app-create-question',
  templateUrl: './create-question.component.html',
  styleUrls: ['./create-question.component.scss']
})
export class CreateQuestionComponent implements OnInit {
  validateForm!: FormGroup;
  checked : boolean;
  listOfControl: Array<{ id: number; controlInstance: string }> = [];

  @Input() question : Question

  addField(e?: MouseEvent): void {
    if (e) {
      e.preventDefault();
    }
    const id = this.listOfControl.length > 0 ? this.listOfControl[this.listOfControl.length - 1].id + 1 : 0;

    const control = {
      id,
      controlInstance: `answer${id}`
    };
    const index = this.listOfControl.push(control);
    this.validateForm.addControl(this.listOfControl[index - 1].controlInstance, new FormControl(null, Validators.required));
  }

  removeField(i: { id: number; controlInstance: string }, e: MouseEvent): void {
    if(!this.checked){
      e.preventDefault();
      if (this.listOfControl.length > 1) {
        const index = this.listOfControl.indexOf(i);
        this.listOfControl.splice(index, 1);
        this.validateForm.removeControl(i.controlInstance);
      }
    }
  }

  submitForm(): void {
    let y = 0;
    for (const i in this.validateForm.controls) {
      if(this.validateForm.controls[i].value == null)
        y = 1;
      this.validateForm.controls[i].updateValueAndValidity();
      this.validateForm.controls[i].markAsDirty();
    }
    if (y == 0){
      this.checked = true;
      let first = 0;
      for (const i in this.validateForm.controls) {
        if(first == 0){
          this.question.setQuestion(this.validateForm.controls[i].value);
          first = 1;
        }
        else
          this.question.multipleChoiceAnswers.push(this.validateForm.controls[i].value);
      }
    }
  }

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.validateForm = this.fb.group({});
    this.addField();
    this.addField();
    this.checked = false;
  }

  //----------------------------------


}
