import { User } from 'src/app/models/essential/user.model';

export class Group {

    private _id: string;

    constructor(
        private _name: string,
        private _description: String,
        private _employees: Array<User> = [],
        private _creator: User | null
    ) { }

    set id(value: string) {
        this._id = value
    }
    get name(): string {
        return this._name;
    }
    get description(): String {
        return this._description;
    }

    get creator(): User {
        return this._creator;
    }

    addEmployee(user: User) {
        this._employees.push(user);
    }

    removeEmployee(user: User) {
        this._employees.filter(u => u.id !== user.id)
    }

    get employees(): Array<User> {
        return this._employees;
    }

}
