import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NzButtonSize } from 'ng-zorro-antd';
import { switchMap } from 'rxjs/operators';
import { Group } from 'src/app/models/essential/group.model';
import { GroupService } from 'src/app/services/group.service';
import {Observable} from 'rxjs'
@Component({
  selector: 'app-manage-group',
  templateUrl: './manage-group.component.html',
  styleUrls: ['./manage-group.component.scss']
})
export class ManageGroupComponent implements OnInit {
  size: NzButtonSize = 'large';
  private _fetchGroups$: Observable<Group[]>
  private _userId  = localStorage.getItem("userid")
  constructor(
    private _groupService : GroupService,
    private router: Router,
    private _route: ActivatedRoute,
  ) {

    this._fetchGroups$ = this._route.queryParams
    .pipe(
      switchMap((newParams) => {
        return this._groupService.getUserGroups$(this._userId);
      })
    )
    
   }

  ngOnInit(): void {
  }

  get groups$(){
    return this._fetchGroups$
  }



}
