import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './components/home/dashboard/dashboard.component';
import { LoginComponent } from './components/user/login/login.component';
import { RegisterComponent } from './components/user/register/register.component';

import { GroupOverviewComponent } from './components/group/group-overview/group-overview.component';
import { GroupDetailsComponent } from './components/group/group-details/group-details.component';
import { ManageUsersComponent } from './components/user/manage-users/manage-users.component';
import { InitiativeOverviewComponent } from './components/initiative/initiative-overview/initiative-overview.component';
import { CreateInitiativeComponent } from './components/initiative/create-initiative/create-initiative.component';
import { InitiativePreparationComponent } from './components/initiative/initiative-preparation/initiative-preparation.component';
import { InitiativeRealizationComponent } from './components/initiative/initiative-realization/initiative-realization.component';

import { GroupPageComponent } from './components/group/group-page/group-page.component';
import { UserDetailsComponent } from './components/user/user-details/user-details.component';
import { InitiativeValueCreationComponent } from './components/initiative/initiative-value-creation/initiative-value-creation.component';
import { ForgotPasswordComponent } from './components/user/forgot-password/forgot-password.component';
import { ChangePasswordComponent } from './components/user/change-password/change-password.component';


import { CreateAdministratorComponent } from './components/user/create-administrator/create-administrator.component';
import { CreateCompanyComponent } from './components/user/create-company/create-company.component';
import { SurveyOverviewComponent } from './components/survey/survey-overview/survey-overview.component';
import { CreateSurveyComponent } from './components/survey/create-survey/create-survey.component';
import { CreateGroupComponent } from './components/group/create-group/create-group.component';
import { ManageGroupComponent } from './components/group/manage-group/manage-group.component';


const routes: Routes = [
  { path: '', component: DashboardComponent },
  { path: 'login', component: LoginComponent },
  { path: 'forgot-password', component: ForgotPasswordComponent },
  { path: 'change-password/:token', component: ChangePasswordComponent },
  { path: 'register', component: RegisterComponent },

  { path: "managegroups", component: GroupPageComponent },
  { path: "user-details", component: UserDetailsComponent },
  { path: "group-overview/:id", component: GroupOverviewComponent },
  { path: "group_detail/:id", component: GroupDetailsComponent },
  { path: "manage-users", component: ManageUsersComponent },
  { path: "initiative-overview", component: InitiativeOverviewComponent },
  { path: "create-initiative", component: CreateInitiativeComponent },
  { path: "initiative-preparation/:id", component: InitiativePreparationComponent },
  { path: "initiative-realization/:id", component: InitiativeRealizationComponent },
  { path: "initiative-value-creation/:id", component: InitiativeValueCreationComponent },
  { path: "survey-overview/:id", component: SurveyOverviewComponent },
  { path: "create-survey/:id", component: CreateSurveyComponent },


  { path: "manage-group/:id", component: ManageGroupComponent },

  { path: 'createAdministrator', component: CreateAdministratorComponent },
  { path: 'create-company', component: CreateCompanyComponent },
  { path: 'create-group', component: CreateGroupComponent },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule { }
