import { Injectable } from '@angular/core';
import { stat } from 'fs';
import { AddMultipeChoiceAnswerToAQuestionGQL, AddQuestionToSurveyGQL, CreateSurveyGQL } from 'src/generated/graphql';

@Injectable({
  providedIn: 'root'
})
export class SurveyService {

  constructor(
    private addSurvey: CreateSurveyGQL,
    private addQuestion: AddQuestionToSurveyGQL,
    private addMultipleChoiceAnswer: AddMultipeChoiceAnswerToAQuestionGQL

  ) { }

  addSurveyToInitiative(initiativeId: string, name: string, category: string, status: string){
    return this.addSurvey.mutate({initiativeId:initiativeId, name:name, category:category, status:status} );
  }

  addQuestionToSurvey(surveyId: string, questionTitle: string){
    return this.addQuestion.mutate({surveyId:surveyId, questionTitle:questionTitle} );
  }

  addMultipleChoiceAnswerToQuestion(questionId: string, answerText: string){
    return this.addMultipleChoiceAnswer.mutate({questionId:questionId, answerText: answerText} );
  }



  
}
