import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SurveyStatus } from 'src/app/models/enum/survey-status.enum';
import { Survey } from 'src/app/models/initiative/survey';
import { Question } from 'src/app/models/initiative/question';
import { trigger } from '@angular/animations';
import { SurveyService } from 'src/app/services/survey.service';
import { ActivatedRoute } from '@angular/router';
import { map, mergeMap, tap } from 'rxjs/operators';
import { merge } from 'rxjs';



@Component({
  selector: 'app-create-survey',
  templateUrl: './create-survey.component.html',
  styleUrls: ['./create-survey.component.scss']
})
export class CreateSurveyComponent implements OnInit {

  private _initiativeId = this._route.snapshot.paramMap.get("id")

  validateForm!: FormGroup;
  isSubmitted : boolean = false
  buttonName: string="Create"
  title : string ="Title of the survey"
  initTitle : string
  nQuestions : number
  name : string
  category: string
  survey: Survey
  errors: boolean
  checked: boolean
  questions: Array<Question> = []
  constructor(
    private fb: FormBuilder,
    private _surveyService : SurveyService,
    private _route: ActivatedRoute
    ) { }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      name: [null, [Validators.required]],
      nQuestions: [null, [Validators.required]],
      surveyCategory : [null, [Validators.required]],
      nickname: [null],
      required: [false]
    });
  }

  submitForm(){
    
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }
    if(this.validateForm.valid){

      this.name = this.validateForm.controls.name.value;

      this.nQuestions = this.validateForm.controls.nQuestions.value

      let locCategory :string = this.validateForm.controls.surveyCategory.value
      switch (locCategory){
        case '0': this.category = "Awareness"; break;
        case '1': this.category = "Receptiveness"; break;
        case '2': this.category = "Implementation"; break;
        case '3': this.category = "Sustaining"; break;
        case '4': this.category = "Evaluation"; break;
      }

      for(let i = 0; i < this.nQuestions; i++){
        this.questions.push(new Question("", []));
      }
      
      
      this.survey = new Survey("TBD", locCategory, this.questions, this.name);


      document.getElementById("form").hidden = true;
      document.getElementById("question").hidden = false;
    }
  }

  addQuestion(surveyId: string, questionText:string){

    this._surveyService.addQuestionToSurvey(surveyId,questionText).pipe()
  }

  addAnswerToQuestion(){
    var ul = document.getElementById("ul")
    ul.insertAdjacentHTML("afterbegin", "<")

  }

  checkEm(): void{
    this.errors = false;

    for(let q in this.survey.questions){
      if (this.survey.questions[q].question == "")
        
        this.errors = true;

     }
     this.checked = !this.errors;
     if(!this.errors){
      this._surveyService
      .addSurveyToInitiative(this._initiativeId, this.survey.title, this.survey.category, this.survey.status)
      .subscribe(surveyData =>{
        
        this.survey.questions.forEach(question => this.addQuestion(surveyData.data.createSurvey.survey.id, question.question))
      })
    }
  }



}
