import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of, pipe } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { GetUserGroupsGQL } from 'src/generated/graphql';
import { Group } from '../models/essential/group.model';

@Injectable({
  providedIn: 'root',
})
export class GroupService {
  private _groups$ = new BehaviorSubject<boolean>(true);

  constructor(private _getUserGroups: GetUserGroupsGQL) {}

  getUserGroups(userId): Observable<Group[]> {
    let list: Array<Group> = new Array();

    this._getUserGroups.mutate({ userId: userId }).subscribe((data) => {
      data.data.getUserGroups.map((t) => {
        let group = new Group(t.name, 'Desciption', null, null);
        group.id = t.id;
        list.push(group);
      });
    });
    return of(list);
  }

  getUserGroups$(userId) {
    return this._groups$.pipe(switchMap(() => this.getUserGroups(userId)));
  }
}
