import { Injectable } from '@angular/core';
import { GetInitiatveGroupsGQL, GetSurveysOfInitiativeGQL } from 'src/generated/graphql';
import { SurveyCategory } from '../models/enum/survey-category.enum';
import { SurveyStatus } from '../models/enum/survey-status.enum';
import { Survey } from '../models/initiative/survey';
import {BehaviorSubject, Observable, of, pipe} from 'rxjs'
import { map, switchMap, tap } from 'rxjs/operators';
import { Group } from '../models/essential/group.model';


@Injectable({
  providedIn: 'root'
})
export class InitiativeService {
  private _groups$ = new BehaviorSubject<boolean>(true);

  constructor( 
    private getSurveysOfInitiative: GetSurveysOfInitiativeGQL,
    private getInitiativeGroups : GetInitiatveGroupsGQL
 ) { }


 getSurveysOfAnInitiative(initiativeId){
  return this.getSurveysOfInitiative.mutate({initiativeId: initiativeId})
} 

surveys$(id:string): Observable<Survey[]>{
  let list : Array<Survey> = new Array
  this.getSurveysOfAnInitiative(id).subscribe(data => {
    data.data.getSurveysOfInitiative.map(t => {
      list.push(new Survey("TBD","AWARNESS",null,t.name))
    })
  })
  return of(list)
}

initiativeGroups(id:string): Observable<Group[]>{
  let list : Array<Group> = new Array
  this.getInitiativeGroups.mutate({initiativeId: id}).subscribe(data => {
    data.data.getInitiatveGroups.map(t => {
      list.push(new Group(t.name, "Desciption", null, null))
    })
  })
  return of(list)
}

  getAInitiativeGroups$(initiativeId){
    return this._groups$.pipe(
      switchMap(() => this.initiativeGroups(initiativeId))
    );
  } 
}
