import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { navigateHome, navigateInitiativeOverview } from 'src/app/utils/navigation';
import { FieldError } from 'src/generated/graphql';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
})
export class RegisterComponent implements OnInit {
  validateForm!: FormGroup;
  graphQLErrors: FieldError[] = [];

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      email: [null, [Validators.email, Validators.required]],
      password: [null, [Validators.required]],
      checkPassword: [null, [Validators.required, this.confirmationValidator]],
      username: [null, [Validators.required]],
      captcha: [null, [Validators.required]],
    });
  }

  submitForm(): void {
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }

    if (this.validateForm.valid) {
      this.userService
        .register(
          this.validateForm.controls.username.value,
          this.validateForm.controls.password.value,
          this.validateForm.controls.email.value
        )
        .subscribe(
          ({ data }) => {
            if (data.register.errors) {
              this.graphQLErrors = data.register.errors;
            } else {
              // register successful
              this.graphQLErrors = [];
              this.userService.checkLoggedIn().refetch();
              localStorage.setItem("userid",data.register.user.id )
              navigateInitiativeOverview(this.router);
            }
          },
          (error) => {
            console.log(error);
          }
        );
    }
  }

  async updateConfirmValidator() {
    await Promise.resolve()
      .then(() =>
        this.validateForm.controls.checkPassword.updateValueAndValidity()
      )
      .catch((err) => {
        console.log(err);
      });
  }

  confirmationValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { required: true };
    } else if (control.value !== this.validateForm.controls.password.value) {
      return { confirm: true, error: true };
    }
    return {};
  };

  getCaptcha(e: MouseEvent): void {
    e.preventDefault();
  }
}
