import { UserRole } from '../enum/user-role.enum';
import { Initiative } from '../initiative/initiative';


export class User {
  private _initiatives: Array<Initiative> = new Array<Initiative>()

  private _id: number;
  constructor(
    private _username: string,
    private _firstname: string,
    private _lastname: string,
    private _email: string,
    private _factroy: string,
    private _roles: Set<UserRole>,
  ) { }

  get id(): number {
    return this._id;
  }

  get name(): string {
    return this._username;
  }

  get firstname(): string {
    return this._firstname;
  }

  get lastname(): string {
    return this._lastname;
  }

  get email(): string {
    return this._email;
  }

  get factory(): string {
    return this._factroy;
  }

  set name(value :string) {
    this._username = value;
  }

  set firstname(value :string){
   this._firstname = value;
  }

  set lastname(value :string) {
     this._lastname = value;
  }

  set email(value :string){
    this._email = value;
  }

  set factory(value :string){
    this._factroy = value;
  }

  get roles(): Set<UserRole> {
    return this._roles
  }

  set roles(value: Set<UserRole>){
    this._roles = value;
  }

  addRole(newRole: UserRole) {
    this._roles.add(newRole)
  }

  removeRole(role: UserRole) {
    this._roles.delete(role)
  }

  updateRole(oldRole: UserRole, newRole: UserRole) {
    this.removeRole(oldRole)
    this.addRole(newRole)
  }


  get initiatives(): Array<Initiative> {
    return this._initiatives
  }

  addInitiative(newInitiative: Initiative) {
    this._initiatives.push(newInitiative)
  }


}

