import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UserService } from 'src/app/services/user.service';
import { UserRole } from 'src/app/models/enum/user-role.enum';
import { User } from 'src/app/models/essential/user.model';

@Component({
  selector: 'app-manage-users',
  templateUrl: './manage-users.component.html',
  styleUrls: ['./manage-users.component.scss']
})
export class ManageUsersComponent implements OnInit {

  public usersToManege: Array<User>;
  public validateForm: FormGroup;
  public searchForm: FormGroup;
  public selecteduser: User;
  public selectedRole: Set<UserRole>;
  public updatesucceeded: any = undefined;
  public failupdatemessage: string;
  

  public userroles: Array<String> = new Array<String>("MANAGER", "CMANAGER", "EADMINISTRATOR", "EMPLOYEE", "SPONSOR")

  constructor(private _fb: FormBuilder, private _userService: UserService) {
    this.usersToManege = new Array<User>();
    console.log(this.usersToManege);
    _userService.getUsers().subscribe((values => {
      values.data.getUsers.forEach(e => {
        var r = new Set<UserRole>();
        e.role.forEach((e) => {
          switch (e) {
            case "EADMINISTRATOR":
              r.add(UserRole.EADMINISTRATOR);
              break;
            case "CMANAGER":
              r.add(UserRole.CMANAGER);
              break;
            case "MANAGER":
              r.add(UserRole.MANAGER);
              break;
            case "EMPLOYEE":
              r.add(UserRole.EMPLOYEE);
              break;
            case "SPONSOR":
              r.add(UserRole.SPONSOR);
              break;
            default:
              break;
          }
        })
        this.usersToManege.push(new User(e.username, e.name, e.lastname, e.email, e.factory, r))
      });
    }));

    this.searchForm = this._fb.group({
      search: [""]
    });

    this.validateForm = this._fb.group({
      name: ["", [Validators.required]],
      lastname: ["", [Validators.required]],
      email: ["", [Validators.email, Validators.required]],
      factory: ["", [Validators.required]],
    });
  }

  ngOnInit(): void { }

  submitForm(value: { name: string; lastname: string; email: string; factory: string; }): void {
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsDirty();
      this.validateForm.controls[key].updateValueAndValidity();
    }

    this.selecteduser.firstname = value.name;
    this.selecteduser.lastname = value.lastname;
    this.selecteduser.factory = value.factory;
    this.selecteduser.email = value.email;


    this._userService.updateUserInformation(this.selecteduser).subscribe((e) => {
      if (e.data.updateUserInformation.errors) { console.log(e); this.updatesucceeded = false; this.failupdatemessage = e.data.updateUserInformation.errors[0].message } else { this.updatesucceeded = true; }
    })
  }

  log(value: string[]): void {
    if (this.selecteduser != undefined) {
      var r = new Set<UserRole>();
      value.forEach((e) => {
        switch (e) {
          case "EADMINISTRATOR":
            r.add(UserRole.EADMINISTRATOR);
            break;
          case "CMANAGER":
            r.add(UserRole.CMANAGER);
            break;
          case "MANAGER":
            r.add(UserRole.MANAGER);
            break;
          case "EMPLOYEE":
            r.add(UserRole.EMPLOYEE);
            break;
          case "SPONSOR":
            r.add(UserRole.SPONSOR);
            break;
          default:
            break;
        }
      })
    }
    this.selecteduser.roles = r;
  }

  search(value: { search }) {
    if (value.search != "") {
      this.usersToManege = new Array<User>();
      this._userService.getUsersWithNames(value.search).subscribe((values => {
        values.data.getUsersWithName.forEach(e => {
          var r = new Set<UserRole>();
          e.role.forEach((e) => {
            switch (e) {
              case "EADMINISTRATOR":
                r.add(UserRole.EADMINISTRATOR);
                break;
              case "CMANAGER":
                r.add(UserRole.CMANAGER);
                break;
              case "MANAGER":
                r.add(UserRole.MANAGER);
                break;
              case "EMPLOYEE":
                r.add(UserRole.EMPLOYEE);
                break;
              case "SPONSOR":
                r.add(UserRole.SPONSOR);
                break;
              default:
                break;
            }
          })
          this.usersToManege.push(new User(e.username, e.name, e.lastname, e.email, e.factory, r))
        });
      }));
    } else {
      this.usersToManege = new Array<User>();
      console.log("ciao");
      this._userService.getUsers().subscribe((values => {
        values.data.getUsers.forEach(e => {
          var r = new Set<UserRole>();
          e.role.forEach((e) => {
            switch (e) {
              case "EADMINISTRATOR":
                r.add(UserRole.EADMINISTRATOR);
                break;
              case "CMANAGER":
                r.add(UserRole.CMANAGER);
                break;
              case "MANAGER":
                r.add(UserRole.MANAGER);
                break;
              case "EMPLOYEE":
                r.add(UserRole.EMPLOYEE);
                break;
              case "SPONSOR":
                r.add(UserRole.SPONSOR);
                break;
              default:
                break;
            }
          })
          this.usersToManege.push(new User(e.username, e.name, e.lastname, e.email, e.factory, r))
        });
      }));
    }
  }

  nextUser(user: User) {
    this.updatesucceeded = undefined;
    console.log(user);
    this.selecteduser = user;
    this.validateForm = this._fb.group({
      name: [user.name, [Validators.required]],
      lastname: [user.lastname, [Validators.required]],
      email: [user.email, [Validators.email, Validators.required]],
      factory: [user.factory, [Validators.required]]
    });
  }

  resetForm(e: MouseEvent): void {
    e.preventDefault();
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
}

