import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { navigateHome, navigateGroupDetail, navigateGroupOverview, navigateInitiativeOverview } from 'src/app/utils/navigation';
import { FieldError } from 'src/generated/graphql';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  validateForm!: FormGroup;
  graphQLErrors: FieldError[] = [];

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      userNameOrEmail: [null, [Validators.required]],
      password: [null, [Validators.required]],
    });
  }

  submitForm(): void {
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }
    if (this.validateForm.valid) {
      this.userService
        .login(
          this.validateForm.controls.userNameOrEmail.value,
          this.validateForm.controls.password.value
        )
        .subscribe(
          ({ data }) => {
            if (data.login.errors) {
              this.graphQLErrors = data.login.errors;
            } else {
              // login successful
              this.graphQLErrors = [];
              this.userService.checkLoggedIn().refetch();
              localStorage.setItem("userid",data.login.user.id )
              navigateInitiativeOverview(this.router)
            }
          },
          (error) => {
            // Internal errors
            console.log(error);
          }
        );
    }
  }
}
