import { Injectable } from '@angular/core';

import { Apollo, gql } from 'apollo-angular';

import {
  CheckLoggedInGQL,
  GetUsersWithNameGQL,
  LoginGQL,
  LogoutGQL,
  RegisterGQL,
  UpdateUserInformationGQL,
  GetUsersGQL,
  ForgotPasswordGQL,
  GetCompanyByUserIdGQL,
  RegisterMultipleGQL,
} from 'src/generated/graphql';
import { UserRole } from '../models/enum/user-role.enum';
import { User } from '../models/essential/user.model';


@Injectable({
  providedIn: 'root',
})
export class UserService {

  constructor(
    private loginGQL: LoginGQL,
    private registerGQL: RegisterGQL,
    private checkLoggedInGQL: CheckLoggedInGQL,
    private logoutGQL: LogoutGQL,

    private getUsersWithName: GetUsersWithNameGQL,
    private updateUserInfo: UpdateUserInformationGQL,
    private getAllUsers: GetUsersGQL,
    private forgotPasswordGQL: ForgotPasswordGQL,
    private getUserCompany : GetCompanyByUserIdGQL,

    private registerMultipleGQL:RegisterMultipleGQL,
    
  ) {}


  login(usernameOrEmail: string, password: string) {
    return this.loginGQL.mutate({ usernameOrEmail, password });
  }

  register(username: string, password: string, email: string) {
    return this.registerGQL.mutate({ input: { username, email, password } });
  }

  registerMultiple(users:string) {
    console.log(users);
    return this.registerMultipleGQL.mutate({input:users});
  }

  checkLoggedIn() {
    return this.checkLoggedInGQL.watch();
  }

  logout() {
    return this.logoutGQL.mutate();
  }

  updateUserInformation(user:User) {
    var r = []
    user.roles.forEach((e)=>{
      r.push(UserRole[e.toString()]);
    })
    console.log(r.toString())
    return this.updateUserInfo.mutate({ email:user.email, role:r.toString(), factory:user.factory, lastname:user.lastname, name:user.firstname, username:user.name });
  }

  getUsersWithNames(name: string) {
    return this.getUsersWithName.fetch({ name });
  }

  getUsers() {
    return this.getAllUsers.fetch();
  }

  forgotPassword(email: string) {
    return this.forgotPasswordGQL.mutate({ email })
  }

  getUserCompanyByUserId(userId:string){
    return this.getUserCompany.mutate({userId})
  }


}

      
