import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Initiative } from 'src/app/models/initiative/initiative';
import { PreparationState } from 'src/app/models/initiative/PreparationState';
import { RealizationState } from 'src/app/models/initiative/RealizationState';
import { ValueCreationState } from 'src/app/models/initiative/ValueCreationState';
import { UserService } from 'src/app/services/user.service';
import { CompanyService } from 'src/app/services/company.service';
import { InitiativeState } from 'src/app/models/initiative/InitiativeState';
import { switchMap } from 'rxjs/operators';
import { NzButtonSize } from 'ng-zorro-antd';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-initiative-overview',
  templateUrl: './initiative-overview.component.html',
  styleUrls: ['./initiative-overview.component.scss']
})
export class InitiativeOverviewComponent implements OnInit {
  size: NzButtonSize = 'large';
  public _initiatives: Array<Initiative> 
  private _userId : string
  private _companyId :string
  succeeded:boolean ;
  error: string=""


  

  constructor(private router: Router, private route: ActivatedRoute, private _userService : UserService, private _companyService: CompanyService) { 
      this._initiatives = new Array()
  }

  ngOnInit(): void {
    this._userId = localStorage.getItem("userid") 

     this._companyId="5fbd23d5fbb32665c4ae3cf5"
     
    this._companyService.getInitiatives(this._companyId).subscribe((data) => {
      data.data.getInitiativesByCompanyId.map(init => {
        var initiative = new Initiative(init.title)
        initiative.id = init.id 
        var state : InitiativeState = this.convertState(init.currentState, initiative)
        initiative.SetSate(state)
        initiative.deadline = init.deadline
        if(init.creator==this._userId){
          initiative.isOwener = true
        }  
        this._initiatives.push(initiative)
      })
    })
  }

  isChangeManager(): boolean{
    return false;
  }
  convertState(state:string, init : Initiative){
    if(state.includes("realization")){
       return new RealizationState(init);
    }
    else if(state.includes("evaluation")){
       return new ValueCreationState(init);
    }
       return new PreparationState(init)
  }

  OnRemoveClick(initiativeId: string){
    if(window.confirm("Are you sure you want to delete this initiative?")){
      this._companyService.deleteInitiative(this._companyId,initiativeId).subscribe(t => {
        if(t.data.removeInitiative){
          this.succeeded = true
          window.location.reload();
        }else {
          this.succeeded = false
          this.error  = "Something went wrong.."
        }
      })
    }
  }

  OnViewSurveyClick(id: string){
    if(id){
      this.router.navigate(['/survey-overview', id]);
    }
         
  }

  OnViewGroupClick(id: string){
    this.router.navigate(['/group-overview', id]);
  }


  }





    
    

  

