import { User } from 'src/app/models/essential/user.model';
import { Group } from '../essential/group.model';
import { InitiativeState } from './InitiativeState';
import { PreparationState } from './PreparationState';
import { RealizationState } from './RealizationState';
import { Survey } from './survey';

export class Initiative {
  private _id: string;
  private _currentState: InitiativeState
  private _groups: Array<Group> = new Array<Group>()
  private _survies: Array<Survey> = new Array<Survey>()
  private _deadline : Date 
  private _creator : User
  private _isOwner : boolean = false

  constructor(private _title: string,) { }


  get title(): String {
    return this._title;
  }

  get isOwner(): boolean{
    return this._isOwner
  }

  set isOwener(value: boolean){
    this._isOwner = value
  }


  public setCreator(user : User){
    this._creator = user
  }

  get creator(): User{
    return this._creator
  }
  get groups(): Array<Group> {
    return this._groups;
  }

  set groups(groups : Array<Group>) {
    this._groups = groups
  }

  get deadline() :Date{
    return this._deadline
  }

  set deadline(date : Date){
    this._deadline = date
  }

  get id(): string {
    return this._id;
  }

  addGroup(newGroup: Group) {
    this._groups.push(newGroup);
  }

  get survies(): Array<Survey> {
    return this._survies;
  }

  addSurvey(newSurvey: Survey) {
    this._survies.push(newSurvey);
  }

  set id(id: string) {
    this._id = id
  }


  get getState(): InitiativeState {
    if (this._currentState == null) {
      this._currentState = new PreparationState(this);
    }
    return this._currentState;
  }

  public SetSate(newState: InitiativeState) {
    this._currentState = newState;
  }

  public getNameState(): string{
    if(this._currentState instanceof PreparationState)
      return "Preparation State"
      else if(this._currentState instanceof RealizationState){
        return "Realization State"
      }else {
        return "Value Creation State"
      }
  }
}
