export class Company{
    name : String;
    employees: User[];

    constructor(name: String){
        this.name = name;
        this.employees = new Array<User>();
    }

    public addEmployee(employee:User) {
        this.employees.push(employee);
    }
}

export class User{
    username: String;

    constructor(name: String){
        this.username = name;
    }
}