import { SurveyCategory } from '../enum/survey-category.enum';
import { SurveyStatus } from '../enum/survey-status.enum';

import { Question } from './question';

export class Survey {

    private _id: number;
    constructor(
        private _status : string,
        private _category: string,
        private _questions : Array<Question>,
        private _title: string    
    ){}

    get status(): string{
        return this._status
    }

    get category(): string{
        return this._category
    }

    get questions(): Array<Question>{
        return this._questions
    }

    get title(): string{
        return this._title
    }
}
