import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Group } from 'src/app/models/essential/group.model';
import { GroupService } from 'src/app/services/group.service';

@Component({
  selector: 'app-initiative-preparation',
  templateUrl: './initiative-preparation.component.html',
  styleUrls: ['./initiative-preparation.component.scss']
})
export class InitiativePreparationComponent implements OnInit {

  ToggleButton: boolean = true;
  public groups: Array<Group> = []

  // public groups : Set<Group> = new Set<Group>(groups)


  private initiativeId: number
  constructor(
    private groupService: GroupService,
    private router: Router,
    private _route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    let id = Number.parseInt(this._route.snapshot.paramMap.get("id"))
    this.initiativeId = id
    let init = null
    // initiatives.find(ini => ini.id == id)
    if (init) {
      this.groups = init.groups
    }
  }

  addGroup(name: String): void {
    const index: number = this.groups.indexOf(this.groups.find(g => g.name == name));
    if (index !== -1) {
      this.groups.splice(index, 1);
      this.ToggleButton = false
    }

  }

  OnGroupClick(id: number) {
    this.router.navigate(['/group_detail', id]);

  }

  goToRealization() {
    this.router.navigate(['/initiative-realization', this.initiativeId]);
  }



}


