import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GroupService } from 'src/app/services/group.service';
import { navigateGroupDetail } from 'src/app/utils/navigation';
import { Group } from 'src/app/models/essential/group.model';
import { InitiativeService } from 'src/app/services/initiative.service';
import { switchMap } from 'rxjs/operators';
import { Observable } from 'rxjs'
import { NzButtonSize } from 'ng-zorro-antd';


@Component({
  selector: 'app-group-overview',

  templateUrl: './group-overview.component.html',
  styleUrls: ['./group-overview.component.scss']
})
export class GroupOverviewComponent implements OnInit {

  private _initiativeId = this._route.snapshot.paramMap.get("id")
  private _fetchGroups$: Observable<Group[]>
  size: NzButtonSize = 'large';


  constructor(
    private router: Router,
    private _route: ActivatedRoute,
    private _initiativeService :InitiativeService,


  ) {

    this._fetchGroups$ = this._route.queryParams
    .pipe(
      switchMap((newParams) => {

        return this._initiativeService.getAInitiativeGroups$(this._initiativeId);
      })
    )
   }

  ngOnInit(): void {

  }

  OnGroupClick(id: number) {
    this.router.navigate(['/group_detail', id]);

  }

  get groups$(){
    return this._fetchGroups$
  }



}
