import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NzButtonSize } from 'ng-zorro-antd';
import { NzSelectModule } from 'ng-zorro-antd/select';
import { switchMap } from 'rxjs/operators';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-create-group',
  templateUrl: './create-group.component.html',
  styleUrls: ['./create-group.component.scss'],
})
export class CreateGroupComponent implements OnInit {
  listOfDepartement: string[] = [];
  listOfSelectedDepartement = [];
  groupForm!: FormGroup;
  activeAddAll: boolean = false
  listOfCountries: string[] = [];
  listOfSelectedCountries = [];

  constructor(
    private _companyService: CompanyService,
    private router: Router,
    private _route: ActivatedRoute,
    private fb: FormBuilder

  ) {
    this.listOfDepartement = this._companyService.getDepartmentsOfCompany(
      '5fbd23d5fbb32665c4ae3cf5'
    );
  }

  ngOnInit(): void {
    this.groupForm = this.fb.group({
      name: [null, [Validators.required]]
    });
  }

  create() {
    for (const i in this.groupForm.controls) {
      this.groupForm.controls[i].markAsDirty();
      this.groupForm.controls[i].updateValueAndValidity();
    }
    if(this.groupForm.valid  ){
      console.log("yesssssssssssssss")
    }
    
  }


}
