import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Survey } from 'src/app/models/initiative/survey';
import { InitiativeService } from 'src/app/services/initiative.service';
import { Observable } from '@apollo/client/core';
import { NzButtonSize } from 'ng-zorro-antd/button';

@Component({
  selector: 'app-survey-overview',
  templateUrl: './survey-overview.component.html',
  styleUrls: ['./survey-overview.component.scss']
})
export class SurveyOverviewComponent implements OnInit {

  private _reloadSurveys$ :  Observable<Survey[]>;
  private _surveys: Survey[];
  private _initiativeId = this._route.snapshot.paramMap.get("id")
  size: NzButtonSize = 'large';

  // private _fetchSurveys$: Observable<Survey[]> = this._initiativeService.surveys$(this._route.snapshot.paramMap.get("id"));
  // private _surveys : Observable<Survey[]>
  constructor(
    private _initiativeService : InitiativeService,
    private router: Router,
    private _route: ActivatedRoute) { }

  ngOnInit(): void {

    this._initiativeService.surveys$(this._initiativeId).subscribe(res => {
      this._surveys = res
    }
    )


  }

  get surveys$(): Survey[]{
    return this._surveys
  }

  onCreate(){
    this.router.navigate(['/create-survey', this._initiativeId]);

  }

}
