import { Injectable } from '@angular/core';
import {
  CreateAdministratorGQL,
  CreateCompanyGQL,
  CreateInitiativeGQL,
  GetCompaniesWithoutAdminGQL,
  GetDepartmentsOfCompanyGQL,
  GetInitiativesByCompanyIdGQL,
  RemoveInitiativeGQL
} from 'src/generated/graphql';

import {BehaviorSubject, of} from 'rxjs'
import { Departement } from '../models/essential/departement.model';
import { switchMap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  private _departments$ = new BehaviorSubject<boolean>(true);


  constructor( 
       private getInitiativesByCompanyId : GetInitiativesByCompanyIdGQL,
       private createAdministratorGQL: CreateAdministratorGQL,
       private GetCompaniesWithoutAdminGQL: GetCompaniesWithoutAdminGQL,
       private createCompanyGQL: CreateCompanyGQL,  
       private createInitiativeGQL: CreateInitiativeGQL,
       private removeInitiative: RemoveInitiativeGQL,
       private _getDeprtmentsOfCompany : GetDepartmentsOfCompanyGQL
    ) { }


  getInitiatives(companyId: string){
    return this.getInitiativesByCompanyId.mutate({companyId})
  }

  createAdministrator(companyName: string, username: string){
    return this.createAdministratorGQL.mutate({input:{companyName, username}});
  }
  getCompaniesWithoutAdmin(){
    return this.GetCompaniesWithoutAdminGQL.watch();
  }

  createCompany(companyName: string){
    return this.createCompanyGQL.mutate({input:companyName});
  }

  createInitiative(state: string, title: string, userId: string, companyId: string, deadline: string){
    return this.createInitiativeGQL.mutate({state:state, title: title,userId: userId, companyId: companyId, deadline: deadline});
  }

  deleteInitiative(companyId, initiativeId){
    return this.removeInitiative.mutate({companyId: companyId, initiativeId:initiativeId})
  }

  getDepartmentsOfCompany(companyId):string[]{
    let list: string[] =[]

    this._getDeprtmentsOfCompany.mutate({ companyId: companyId }).subscribe((data) => {
      data.data.getDepartmentsOfCompany.map((t) => {
        list.push(t.name);
      });
    });
    return list
  }

  // getCompanyDepartments$(companyId) {
  //   return this._departments$.pipe(switchMap(() => this.getDepartmentsOfCompany(companyId)));
  // }


}
