import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { UserService } from 'src/app/services/user.service';
import { navigateInitiativeOverview, navigateLogin } from 'src/app/utils/navigation';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'],
})
export class NavbarComponent implements OnInit {
  private querySubscription: Subscription;
  public username: string = null;

  constructor(private userService: UserService, private router: Router) { }

  ngOnInit(): void {
    this.querySubscription = this.userService
      .checkLoggedIn()
      .valueChanges.subscribe(({ data }) => {
        console.log(data);

        if (data.checkLoggedIn !== null) {
          this.username = data.checkLoggedIn.user.username;
        } else {
          this.username = null;
        }
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
    
  }

  logout() {
    this.userService.logout().subscribe(({ data }) => {
      if (data.logout) {
        console.log('ja');

        this.userService.checkLoggedIn().refetch();
        navigateLogin(this.router);
      }
    });
  }

  initiativeOverview(){
    navigateInitiativeOverview(this.router)
  }

  onManageGroupClick(){
    const userid = localStorage.getItem("userid") 
    this.router.navigate(['/manage-group', userid ]);
  }
}
